import checkIfSolutionIsCorrect from "./checkIfSolutionIsCorrect";
import correctSolution from "./correctSolution";


function performSimpleCrossing(populationAfterSelection, crossingChance=0.75){
    let populationAfterCrossing = [];
    // let firstParent;
    // let secondParent;
    // let firstChild;
    // let secondChild;

    let solutionSize = populationAfterSelection[0].length;  //assert every length is the same
    let sequence = Array.from(Array(solutionSize).keys());
    // console.log(solutionSize)
    // console.log('seq123')
    // console.log(populationAfterSelection)
    // console.log(sequence)

    // console.log('test perform selection')
    // console.log('populationbeforecrossing')
    // console.log(populationAfterSelection)

    let counter = 0;
    do {
        //pick parent 1 and parent 2(random)
        
        // let firstParent = populationAfterSelection[Math.floor(Math.random() * populationAfterSelection.length)]
        // let secondParent = populationAfterSelection[Math.floor(Math.random() * populationAfterSelection.length)]
      
        // console.log(counter)
        let firstParent = populationAfterSelection[counter++];
        // console.log(counter)
        let secondParent = populationAfterSelection[counter++];
        // console.log(counter)

        let firstChild = [...firstParent];
        let secondChild = [...secondParent];

        //if no crossing then children = parents
        if(Math.random() < crossingChance){
            // console.log('')
            // console.log('corssing')
            let crossingPoint = (Math.floor(Math.random() * (firstParent.length)));
            //choose [0-N] crossing point
            firstChild = firstParent.slice(0, crossingPoint).concat(secondParent.slice(crossingPoint));
            secondChild = secondParent.slice(0, crossingPoint).concat(firstParent.slice(crossingPoint)); 
            //todo: remove duplicates and add missing points
            // console.log('before correction ')
            // console.log('firstChild ' + firstChild)
            // console.log('secondChild ' + secondChild)

            if (!checkIfSolutionIsCorrect(firstChild))
                firstChild = correctSolution(firstChild)
            if (!checkIfSolutionIsCorrect(secondChild))
                secondChild = correctSolution(secondChild)

            // console.log('after correction ')
            // console.log('crossingPoint ' + crossingPoint)
            // console.log('firstParent ' + firstParent)
            // console.log('secondParent ' + secondParent)
            // console.log('firstChild ' + firstChild)
            // console.log('secondChild ' + secondChild)
            // console.log('')
        }

        populationAfterCrossing.push(firstChild);
        populationAfterCrossing.push(secondChild);  //second child possibly redundant
    }
    while (populationAfterCrossing.length < populationAfterSelection.length)
    
    console.log('populationAfterCrossing')
    console.log(populationAfterCrossing)
    console.log('')
    console.log(' populationAfterCrossing.length ' +  populationAfterCrossing.length)
    console.log(' populationAfterSelection.length  ' + populationAfterSelection.length )
    console.log(' cut  ' )
    console.log(populationAfterSelection)

    return populationAfterCrossing.length > populationAfterSelection.length ?
        populationAfterCrossing.slice(0, -1) : populationAfterCrossing;
}

export default performSimpleCrossing;