import React, { useEffect, useState } from "react";
import { mapboxglAccessToken, InitialConfigMapboxglUS } from "../../const/constants"
import tspCoords from "../../const/tspCoordsUS_lower48"
import generateRandomSolution from "utils/generateRandomSolution";
import sortCoordsDataBySolution from "utils/sortCoordsDataBySolution"
import mapboxgl from "mapbox-gl";
import './PathMap.scss';

let mapboxglMap;

const setupMapbox = (solutionToDisplay) => {
    mapboxgl.accessToken = mapboxglAccessToken;
    mapboxglMap = new mapboxgl.Map(InitialConfigMapboxglUS);
    
    mapboxglMap.on('load', () => {
        mapboxglMap.addSource('route', {
          'type': 'geojson',
          'data': {
            'type': 'Feature',
            'properties': {},
            'geometry': {
              'type': 'LineString',
              'coordinates': solutionToDisplay
            }
          }
        });
        mapboxglMap.addLayer({
          'id': 'route',
          'type': 'line',
          'source': 'route',
          'layout': {
            // 'text-field': ['get', 'description'],
            'line-join': 'round',
            'line-cap': 'round'
          },
          'paint': {
            'line-color': '#1D62F0',
            'line-width': 1
          }
        });
      });
}

