

function performSwapMutation(populationAfterCrossing, mutationChance=0.025){
    // console.log('test swap 1 ')
    // console.log(populationAfterCrossing)
    let mutatedPopulation = JSON.parse(JSON.stringify(populationAfterCrossing));
    // console.log('mutated pop ')
    // console.log(mutatedPopulation)
    for (let i=0; i<mutatedPopulation.length; i++){
        let solution = mutatedPopulation[i];
            // console.log('mutate attempt')
            // console.log('solution !')
            // console.log(solution)
            for(let j=0; j<solution.length; j++)
                if(Math.random() < mutationChance){
                    // console.log('mutate ')
                    let indexToBeSwapped = Math.floor(Math.random() * solution.length);
                    // console.log('current index ' + j)
                    // console.log('indexToBeSwapped ' + indexToBeSwapped)
                    // console.log('solution before mutate ' + solution)
                    let temp = solution[j];
                    solution[j] = solution[indexToBeSwapped];
                    solution[indexToBeSwapped] = temp;
                    // console.log('solution after mutate ' + solution)
                }
            // populationAfterCrossing[index] = [...solution];
            // console.log('updated?')
            // console.log(populationAfterCrossing[i])
            // mutatedPopulation.push(populationAfterCrossing[index])
    }

    // console.log('test swap 2 ')
    // console.log(mutatedPopulation)

    return mutatedPopulation;
}

export default performSwapMutation;