import calculateSolutionFitness from "./calculateSolutionFitness";
import selectBestSolution from "./selectBestSolution";

function performTournamentSelection(population, tournamentSize, distanceMatrix){
    // console.log('population before selection')
    // console.log(population)
    population.forEach(solution =>{
        calculateSolutionFitness(solution, distanceMatrix)
    })
    // console.log('distanceMatrix ')
    // console.log(distanceMatrix)
    // console.log('tournamentSize ' + tournamentSize)

    let selectedForTournament;
    let populationAfterSelection = [];

do {
    selectedForTournament = [];
    let selectedSolution = null;
    let bestFitnessSoFar = Number.MAX_SAFE_INTEGER;

    for (let i=0; i<tournamentSize; i++)
        selectedForTournament.push(population[Math.floor(Math.random() * population.length)])

    // console.log('test selectedForTournament ')
    // console.log(selectedForTournament)

    //refactor
    // selectedForTournament.forEach(solution => {
    //     let solutionFitness = calculateSolutionFitness(solution, distanceMatrix);
    //     if (solutionFitness < bestFitnessSoFar){ // TSP - minimization problem
    //         bestFitnessSoFar = solutionFitness;
    //         selectedSolution = solution;
    //     }
    // })
    selectedSolution = selectBestSolution(selectedForTournament, distanceMatrix);

    // console.log('selectedSolution ' + selectedSolution)
    // console.log('bestFitnessSoFar ' + bestFitnessSoFar)
    populationAfterSelection.push(selectedSolution);
}
while (populationAfterSelection.length != population.length)

    // console.log('population after tournament selection')
    // console.log(populationAfterSelection)
    populationAfterSelection.forEach(solution =>{
        calculateSolutionFitness(solution, distanceMatrix)
    })

    return populationAfterSelection;
}

export default performTournamentSelection;